export default {"name":"新西兰茶文化简介","menus":[{"content":[{"src":"\\abroads\\new_zealand\\cwhjj.jpg"},{"text":"新西兰是一个畜牧业比较发达的国家，居民多是欧洲移民，长期以来有饮茶习惯，平均每人饮茶量高达4.5公斤，所以茶叶进口占很大比例。"},{"text":"新西兰一向视饮茶为人生一大享受，特别在还带有极浓厚英国色彩的南岛基督堂市等地区。当地居民通常每天喝茶七次，包括喝早茶、早餐茶、午茶、午餐茶、下午茶、晚餐茶和晚茶。且每次都是郑重其事地坐下来喝，每次喝茶，泡红茶、加糖和奶等样样都要齐。许多机关、学校、企业公司等，都特别订出饮茶时间，让大家享受这一传统习惯。"},{"text":"新西兰人茶叶泡制方法，大体使用英国方法——烹煮法。泡茶常用两只壶，一只盛茶，另一只盛热水，茶的浓淡按个人口味调节，但茶的浓度远远高于其他国家。另外，新西兰饮茶都喜欢用牛奶调茶，习惯于加糖加乳，要求汤浓，汤色红艳，喜欢外形粗壮、乌润、紧结重实的大号红碎茶。袋泡茶叶要求浓度好，汤色红颜。"},{"text":"新西兰人每日饮茶4-7次，每次1-3大碗。新西兰人习惯早晨起床后饮茶一大杯，辅以一片涂上奶油的面包，或一些饼干。早饭时又饮茶一大杯。上午十一点进早茶，这不但是家庭中的事，而且为大多数办公室及商场所袭用。全国大多数的居民多在午餐时饮茶。午后四点，家庭、旅馆、饭店、茶室、及办公室，又有茶的供应。当晚餐时饮茶者更多。新西兰的茶室分很多级，一般小店供应面包、奶油、饼干和茶水。大的百货商场设有豪华茶室，在每天上午八时以及下午四时特别拥挤。以社交为目的的早茶和午后茶同样普遍。"}]}]}